.header {
    position: sticky;
    top: 0;
    z-index: 2;
    padding: 1.25em 0;
    background-color: $champagne;
}

.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__logo-wrapper {

}

.header__logo-link {
    @include font-patterns(#000, "TildaSans", 2rem, normal, 400, 130%);
    cursor: pointer;
}

.header__nav {
    display: flex;
    align-items: center;
}

.header__nav-list {
    display: flex;
    align-items: center;
    column-gap: 5em;
}

.header__list-item {

}

.header__item-link {
    @include font-patterns(#000, "TildaSans", 1.25rem, normal, 400, 130%);
    display: inline-block;
    align-items: center;
    text-transform: uppercase;
    cursor: pointer;

    &::after {
        display: block;
        width: 0%;
        height: 2px; 
        background-color: #545454;
        transition: width 0.3s ease-in-out;
        content: "";
    }

    &:hover::after {
        width: 100%;
    }
}

.header__button {
    @include font-patterns(#000, "TildaSans", .87rem, normal, 500, 130%);
    display: flex;
    align-items: center;
    align-self: center;
    justify-content: center;
    width: 130px;
    height: 40px;
    text-transform: uppercase;
    background-color: transparent;
    border: 1px solid #000;
    transition: .2s;

    &:hover {
        background-color: #000;
        color: #fff;
        transition: .2s;
    }
}