/*  FOOTER STYLES WILL BE HERE  */
.footer {
    padding-top: 7.5em;
    padding-bottom: 7.5em;
    background-color: $brown;
}

.footer__container {
    display: flex;
    justify-content: space-between;
}

.footer__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.footer__content-wrapper {
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    row-gap: 2em;
}

.footer__content-title {
    @include font-patterns($gray, "TildaSans", 5.5rem, normal, 600, 130%);
    text-transform: uppercase;
}

.footer__content-mail {
    @include font-patterns($champagne, "TildaSans", 3rem, normal, 500, 130%);
    width: intrinsic;
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
    transition: 0.2s;

    &:hover {
        color: $gray;
        transition: 0.2s;
    }
}

.footer__content-tel {
    @include font-patterns($champagne, "TildaSans", 3rem, normal, 500, 130%);
    width: intrinsic;
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
    transition: 0.2s;

    &:hover {
        color: $gray;
        transition: 0.2s;
    }
}

.footer__content-button {
    @include font-patterns($champagne, "TildaSans", .87rem, normal, 600, 130%);
    display: flex;
    align-self: center;
    justify-content: center;
    align-items: center;
    width: 637px;
    height: 55px;
    padding: 0.5em 1.5em;
    text-transform: uppercase;
    background-color: transparent;
    border: 1px solid $champagne;
    transition: 0.2s;

    &:hover {
        color: #000;
        background-color: $champagne;
        transition: 0.2s;
    }
}

.footer__social-list {
}

.footer__list-item {
    margin-bottom: 0.5em;

    &:nth-last-child(1) {
        margin-bottom: 0;
    }
}

.footer__item-link {
    @include font-patterns($gray, "TildaSans", 1.5rem, normal, 400, 130%);
    transition: 0.2s;

    &:hover {
        color: #fff;
        transition: 0.2s;
    }
}
