.header {
  position: sticky;
  top: 0;
  z-index: 2;
  padding: 1.25em 0;
  background-color: #e7e7e7;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo-link {
  color: #000;
  font-weight: 400;
  font-size: 2rem;
  font-family: "TildaSans";
  font-style: normal;
  line-height: 130%;
  cursor: pointer;
}

.header__nav {
  display: flex;
  align-items: center;
}

.header__nav-list {
  display: flex;
  align-items: center;
  column-gap: 5em;
}

.header__item-link {
  color: #000;
  font-weight: 400;
  font-size: 1.25rem;
  font-family: "TildaSans";
  font-style: normal;
  line-height: 130%;
  display: inline-block;
  align-items: center;
  text-transform: uppercase;
  cursor: pointer;
}
.header__item-link::after {
  display: block;
  width: 0%;
  height: 2px;
  background-color: #545454;
  transition: width 0.3s ease-in-out;
  content: "";
}
.header__item-link:hover::after {
  width: 100%;
}

.header__button {
  color: #000;
  font-weight: 500;
  font-size: 0.87rem;
  font-family: "TildaSans";
  font-style: normal;
  line-height: 130%;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  width: 130px;
  height: 40px;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #000;
  transition: 0.2s;
}
.header__button:hover {
  background-color: #000;
  color: #fff;
  transition: 0.2s;
}

