.architect::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  background-image: url("../images/schema.png");
  background-repeat: no-repeat;
  background-position: 95% bottom;
  background-size: contain;
  opacity: 0.1;
  content: "";
