/* IMPORT ALL STYLES AND MIXINS HERE  */
@import "./components/normalize";
@import "./components/_vars";
@import "./components/_mixins";
@import "./components/_header";
@import "./components/_footer";
@import "./components/_defaults";
@import "./components/_fonts";
@import "./components/media";

.architect {
    position: relative;
    display: flex;
    align-items: center;
    min-height: calc(100vh - 81px);
    padding-top: 7.5em;
    padding-bottom: 7.5em;
    background-color: $champagne;

    &::before {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 0;
        background-image: url("../images/schema.png");
        background-repeat: no-repeat;
        background-position: 95% bottom;
        background-size: contain;
        opacity: 0.10;
        content: "";
    }
}

.arhitect__list {
    display: flex;
    justify-content:space-between;
}

.architect__list-item {
    display: inline-block;

    &:nth-last-child(1) {
        margin: 0 0 0 2em;
    }
}

.architect__item-desc {
    @include font-patterns(#000, "TildaSans", 6.25rem, normal, 600, 140%);
    display: inline-flex;
    align-items: center;
    position: relative;
    z-index: 2;
    text-transform: uppercase;
}

.architect__item-desc::before {
    display: inline-flex;
    align-items: center;
    width: 20px;
    height: 20px;
    margin: 0 16px 0 0;
    background-color: #000;
    border-radius: 50%;
    content: "";
}

.architect__desc {
    @include font-patterns(#7d7f81, "TildaSans", 6.25rem, normal, 600, 140%);
    position: relative;
    z-index: 2;
    text-align: right;
    text-transform: uppercase;
}

.architect__subdesc {
    @include font-patterns(#7d7f81, "TildaSans", 1.25rem, normal, 600, 130%);
    position: relative;
    z-index: 2;
    padding: 1em 0;
    text-align: left;
    text-transform: uppercase;
}

// Portfolio
.portfolio {
    padding-top: 7.5em;
    padding-bottom: 7.5em;
    background-color: $brown;
}

.portfolio__container {
    padding: 0 2em !important;
}

.portfolio__title {
    @include font-patterns($gray, "TildaSans", 5.5rem, normal, 600, 130%);
    padding: 0.5em 0;
    text-transform: uppercase;
}

.portfolio__list {
    display: flex;
    flex-wrap: wrap;
    row-gap: 2em;
}

.portfolio__list-item {
    display: flex;
    max-height: 530px;
    overflow: hidden;
}

.portfolio__item-desc {
    display: flex;
    flex: 1 1 23%;
    justify-content: center;
    background-color: $champagne;
}

.portfolio__desc {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 18.12em;
}

.portfolio__desc-title {
    @include font-patterns(#000, "TildaSans", 3.43rem, normal, 600, 66px);
    margin: 0.25em auto;
    text-align: center;
    text-transform: uppercase;
}

.portfolio__desc-about {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 0 16px;
}

.portfolio__desc-subtitle {
    @include font-patterns(#000, "TildaSans", 1.25rem, normal, 400, 25px);
    display: flex;
    align-self: center;
    justify-content: center;
    padding: 0.5em 1.5em;
    text-align: center;
    text-transform: uppercase;
    background-color: transparent;
    transition: 0.2s;
}

.portfolio__desc-link {
    @include font-patterns(#000, "TildaSans", 1.25rem, normal, 500, 20px);
    width: 240px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1em 0;
    padding: 0.5em 1em;
    text-transform: uppercase;
    background-color: transparent;
    border: 1px solid #000;
    transition: 0.2s;

    &:hover {
        background-color: #000;
        color: #fff;
        cursor: pointer;
        transition: 0.2s;
    }
}

.portfolio__item-img {
    max-height: 503px;
    overflow: hidden;

    &:nth-last-child(1) {
        margin-left: 2em;
    }
}

.last__desc {
    margin-left: 2em;
}

.portfolio__last {
    margin-left: 0 !important;
}

// Services

.services {
    padding-top: 7.5em;
    padding-bottom: 7.5em;
    background-color: $champagne;
}

.services__list-item {
    display: flex;
}

.services__item-img {
    max-width: 539px;
}

.services__item-desc {
    display: flex;
    flex: 1 0 auto;
    flex-direction: column;
    justify-content: space-between;
    max-height: 539px;
    margin-left: 2em;
    background-color: $brown;
}

.services__desc {
    display: flex;
}

.services__desc-title {
    @include font-patterns($gray, "TildaSans", 5.5rem, normal, 600, 130%);
    padding: 0.5em;
    text-transform: uppercase;
}

.services__desc-about {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 25%;
}

.services__desc-subtitle {
    @include font-patterns($champagne, "TildaSans", 1.25rem, normal, 300, 25px);
    display: flex;
    align-self: center;
    justify-content: center;
    padding: 0 0 0 3.75em;
    text-align: center;
    text-transform: uppercase;
    background-color: transparent;
}

.services__desc-link {
    @include font-patterns($champagne, "TildaSans", 1.25rem, normal, 400, 130%);
    display: flex;
    align-self: center;
    align-items: center;
    justify-content: center;
    width: 240px;
    height: 60px;
    margin: 0 3.75em 0 0;
    text-transform: uppercase;
    background-color: transparent;
    border: 1px solid #fff;
    transition: 0.2s;

    &:hover {
        color: #000;
        background-color: $champagne;
        cursor: pointer;
        transition: 0.2s;
    }
}

.services__details {
    position: relative;
    min-width: 100%;
    margin: 1rem auto;
    color: #263238;
    background-color: $brown;
    transition: background-color 0.2s;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        border-radius: inherit;
        box-shadow: 0 0.25em 0.5em #263238;
        opacity: 0.15;
        transition: opacity 0.2s;
        content: "";
        pointer-events: none;
    }

    > :last-child {
        margin-bottom: 1em;
    }

    &[open] {
        background-color: $brown;

        &::before {
            opacity: 0.6;
        }
    }
}

.services__details-title {
    @include font-patterns($champagne, "TildaSans", 2.5rem, normal, 500, 130%);
    position: relative;
    display: block;
    margin: 0 0.5em;
    padding: 0.5em 0;
    text-transform: uppercase;
    cursor: pointer;

    &::before,
    &::after {
        position: absolute;
        top: 50%;
        right: 0;
        width: 0.75em;
        height: 2px;
        text-align: right;
        background-color: $champagne;
        transform: translateY(-50%);
        transition: transform 0.2s ease-in-out;
        content: "";
    }

    &::after {
        transform: translateY(-50%) rotate(90deg);

        [open] & {
            transform: translateY(-50%) rotate(180deg);
        }
    }

    &::-webkit-details-marker {
        display: none;
    }
}

.services__details-wrapper {
    display: flex;
    justify-content: space-between;
    background-color: $champagne;
}

.services__details-list {
    margin: 1em 0 0 1em;
    list-style-type: none;
}

.services__list-details {
    padding: 0.25em 0;

    &::before {
        padding-right: 0.5em;
        color: #000;
        content: "\25CF";
    }

    span {
        @include font-patterns(#7b7b7b, "TildaSans", 1.25rem, normal, 300, 130%);
        text-transform: uppercase;
    }
}

.services__details-price {
    @include font-patterns(#000, "TildaSans", 3rem, normal, 500, 130%);
    display: flex;
    align-items: center;
    margin: 0 16px 0 0;
}

// we work section

.wework {
    padding-top: 7.5em;
    padding-bottom: 7.5em;
}

.wework__title {
    @include font-patterns(#000, "TildaSans", 5.4rem, normal, 600, 130%);
    padding: 0 0 0.5em;
    text-transform: uppercase;
}

.wework__wrapper {
    display: flex;
    align-items: center;
}

.wework__list {
    display: flex;
    flex-wrap: wrap;
    column-gap: 4em;
    row-gap: 5em;
}

.wework__list-item {
    flex: 1 1 400px;
    flex-direction: column;
    justify-content: center;
}

.wework__item-title {
    @include font-patterns(#000, "TildaSans", 3.43rem, normal, 500, 130%);
    width: 50%;
    padding: 0 0 0.25em;
    text-transform: uppercase;
}

.wework__item-subtitle {
    @include font-patterns(#222427, "TildaSans", 1.25rem, normal, 400, 160%);
    text-transform: uppercase;
}

.wework__img {
    width: 40%;
}

// about section

.about {
    padding-top: 7.5em;
    padding-bottom: 7.5em;
    background-color: $champagne;
}

.about__us {
    display: flex;
    column-gap: 3em;

    img {
        max-width: 1060px;
        object-fit: contain;
    }
}

.about__us-desc {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.about__us-title {
    @include font-patterns(#000, "TildaSans", 6.25rem, normal, 600, 130%);
    text-transform: uppercase;
}

.about__us-subtitle {
    @include font-patterns(#222427, "TildaSans", 1.87rem, normal, 400, 130%);
    padding: .3em 0;
    text-transform: uppercase;
}

.about__bio-list {
    display: flex;
    column-gap: 4.5em;
}

.about__list-item {
    display: flex;
    flex: 0 1 31%;
    flex-direction: column;
}

.about__list-title {
    @include font-patterns(#000, "TildaSans", 3.75rem, normal, 500, 140%);
    padding: 50px 0 0;
}

.about__list-position {
    @include font-patterns($gray, "TildaSans", 1.25rem, normal, 400, 140%);
    padding: .25em 0;
    text-transform: uppercase;
}

.about__list-edu {
    @include font-patterns(#000, "TildaSans", 1.25rem, normal, 400, 140%);
    padding-top: .25em;
    text-transform: uppercase;
}